  color: white;
  padding-bottom: 20px
}
a.fa.fa-facebook {
  margin: auto 5px;
  color: #3B5998
}
a.fa.fa-google-plus {
  margin: auto 5px;
  color: #d34836
}
a.fa.fa-linkedin {
  margin: auto 5px;
  color: #007bb6
}
a.fa.fa-twitter {
  margin: auto 5px;
  color: #00aced
}



